.home__list {
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.item {
  border: 2px solid var(--mainBtns-color);
  box-shadow: 0px 3px 0px 0px var(--mainBtns-color);
  background-color: #ffd465;
  border-radius: 16px;
  width: 100%;
  padding: 24px;
  position: relative;
}

.item__title {
  color: #18191f;
  margin: 0 0 20px;
  font-size: 21px;
  font-weight: 900;
}

.item__text {
  color: #474a57;
  margin: 0 0 15px;
  font-size: 17px;
  font-weight: 500;
}

.item__actions {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.item__info {
  flex-wrap: wrap;
  gap: 2px;
  display: flex;
}

.item__info span, .item__author {
  color: #18191f;
  -webkit-user-select: none;
  user-select: none;
  font-size: 12px;
  font-weight: 700;
}

.item__like {
  align-items: center;
  gap: 2px;
  display: flex;
}

.btn-like {
  transition: all .3s ease-in-out;
}

.btn-like.active svg {
  fill: #ffbd12;
}

.btn-like.active {
  animation: .4s ease-in-out alternate likeActive;
}

@media (width <= 768px) {
  .section-title {
    text-align: center;
  }
}

@keyframes likeActive {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(1turn);
  }
}

@media (hover: hover) {
  .btn-like:hover {
    transform: scale(1.2);
  }

  .item__author:hover {
    text-decoration: underline;
  }
}

.item__like span {
  -webkit-user-select: none;
  user-select: none;
}

.item__control {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  display: flex;
  position: absolute;
  top: 25px;
  right: 25px;
}

.item-red {
  background-color: #ff9692;
}

.item-green {
  background-color: #d6fcf7;
}

.item-white {
  background-color: #fff3f8;
}

.item-yellow {
  background-color: #ffd465;
}

.item-pink {
  background-color: #ff89bb;
}

.item__images {
  background-color: #fff3;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  max-width: 450px;
  margin: 0 auto 15px;
  padding: 15px;
  display: flex;
}

.swiper-slide {
  text-align: center;
}

.swiper-pagination {
  position: absolute;
  top: 15px;
  right: 15px;
}

.swiper-pagination-fraction {
  -webkit-user-select: none;
  user-select: none;
  background-color: #fffc;
  border-radius: 16px;
  width: max-content;
  height: max-content;
  padding: 5px;
  font-size: 12px;
  top: 15px;
  left: 15px;
}

.swiper-slide img {
  height: 250px;
}

.filter {
  flex-direction: column;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.filter__label {
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
  display: flex;
  position: relative;
}

.filter__input--btn {
  cursor: pointer;
  background-color: #0000;
  border: none;
  height: 100%;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 0;
  right: 16px;
}

.filter__input, .filter__input.focus-visible {
  box-shadow: 0px 3px 0px 0px var(--mainBtns-color);
  color: var(--mainBtns-color);
  border: 2px solid var(--mainBtns-color);
  background-color: #fff;
  border-radius: 32px;
  width: 100%;
  padding: 8px 48px 8px 16px;
  font-size: 18px;
  font-weight: 700;
}

.filter__btn {
  color: var(--mainBtns-color);
  border: 2px solid var(--mainBtns-color);
  cursor: pointer;
  background-color: #fff;
  border-radius: 32px;
  align-items: center;
  gap: 5px;
  min-height: 45px;
  padding: 2px 4px;
  font-size: 12px;
  transition: transform .3s ease-in-out;
  display: flex;
}

@media (any-hover: hover) {
  .filter__btn:hover {
    transform: scale(.95);
  }

  .filter__input--btn:hover {
    transform: scale(1.2)rotate(90deg);
  }
}

.filter__btn.active {
  box-shadow: 0px 3px 0px 0px var(--mainBtns-color);
  background-color: #ffbd12;
}

.filter__btn span {
  font-weight: 700;
}

.filter__button--svg {
  border: 2px solid var(--mainBtns-color);
  background-color: #ffbd12;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  display: flex;
}

.filter__btn--like .filter__button--svg {
  background-color: #ff89bb;
}

.filter__btn--relevation .filter__button--svg {
  background-color: #d6fcf7;
}

.tags__label {
  width: 100%;
  position: relative;
}

.select-btn {
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  margin-bottom: 8px;
  padding: 6px 15px;
  display: flex;
}

.select-btn .btn-text {
  color: #333;
  font-size: 17px;
  font-weight: 400;
}

.arrow-down {
  color: #fff;
  border: 2px solid var(--mainBtns-color);
  box-shadow: 0px 1px 0px 0px var(--mainBtns-color);
  background: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 14px;
  transition: all .3s;
  display: flex;
}

.select-btn.open .arrow-dwn {
  transform: rotate(-180deg);
}

.tags__box {
  z-index: 6;
  border: 2px solid var(--mainBtns-color);
  background-color: #fff;
  border-radius: 16px;
  width: 100%;
  max-width: 220px;
  max-height: 200px;
  margin-top: 15px;
  padding: 4px;
  display: none;
  position: absolute;
  overflow-y: auto;
}

.tags__items {
  padding: 0;
}

.select-btn.open ~ .tags__box {
  display: block;
}

.tags__items .tag {
  cursor: pointer;
  border-radius: 16px;
  align-items: center;
  height: 50px;
  padding: 0 15px;
  list-style: none;
  transition: all .3s;
  display: flex;
}

.tag:not(:last-child) {
  margin-bottom: 5px;
}

.tag.checked {
  background-color: var(--activeMainBtns);
  border: 2px solid var(--mainBtns-color);
  display: none;
}

.tag.hidden {
  display: none;
}

.tags__checked {
  min-height: 42px;
  display: flex;
}

.arrow-down svg {
  transition: transform .3s ease-in-out;
}

.select-btn.open .arrow-down svg {
  transform: rotate(180deg);
}

.tags__checked {
  justify-content: center;
  gap: 15px;
  margin-bottom: 15px;
  padding: 6px;
  display: flex;
}

.checked-tag {
  box-shadow: 0px 1px 0px 0px var(--mainBtns-color);
  color: var(--mainBtns-color);
  border: 2px solid var(--mainBtns-color);
  background-color: #ffbd12;
  border-radius: 32px;
  align-items: center;
  gap: 5px;
  padding: 2px 4px;
  font-size: 16px;
  font-weight: 500;
  display: flex;
}

.checked-tag__remove {
  cursor: pointer;
  transition: all .3s ease-in-out;
}

@media (hover: hover) {
  .checked-tag__remove:hover, .select-btn:hover .arrow-down {
    transform: scale(.8);
  }

  .tag:hover {
    background-color: var(--activeMainBtns);
  }
}

.tags__search {
  background-color: var(--activeMainBtns);
  border: none;
  border-radius: 16px;
  width: 100%;
  padding: 6px 10px 6px 6px;
}

.tags__search::placeholder {
  font-size: 14px;
}
